* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "montseratt", sans-serif;
}

.App {
  background-image: url("./assets/coldbg.jpeg");
  background-size: cover;
  background-position: bottom;
  transition: 0.5s ease-out;
  width: 100%;
  border-radius: 20px;
}

.App.Warm {
  background-image: url("./assets/warmbg.jpeg");
}

main {
  min-height: 100vh;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.3),
    rgba(0, 0, 0, 0.7)
  );
  border-radius: 20px;
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 0 0 75px;
}

.search-bar {
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 1rem 1rem;
  margin-top: -25px;
  box-shadow: 0px 5px 3px rgba(0, 0, 0, 0.2);
  color: #313131;
  font-size: 20px;
  transition: 0.4s ease;
}

.search-bar:focus {
  background-color: rgba(255, 255, 255, 0.75);
}

.location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.date {
  color: #fff;
  font-size: 20px;
  font-weight: 400;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 1rem;
  padding: 15px 25px;
  color: #fff;
  font-size: 100px;
  font-weight: 900;
  text-shadow: rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.3);
}

.weather {
  color: #fff;
  font-size: 50px;
  font-weight: 700;
  text-shadow: rgba(50, 50, 70, 0.5);
}

@media (min-width: 760px) {
  body {
    background: rgb(160, 151, 151);
  }
  .App {
    width: 500px;
    margin: 0 auto;
    box-shadow: 10px 5px 10px rgba(0, 0, 0, 0.5);
  }
}
